.content {
  display: grid;
  grid-auto-columns: auto 1fr;
  grid-column-gap: 20px;
  grid-row-gap: 2px;
}

.cardEditor label {
  color: white;
}

.content > * {
  grid-column: 1 / 3;
}

.content > label {
  color: white;
  grid-column: 1;
}

.content > input,
.content > select {
  grid-column: 2;
}

.content input[type="color"] {
  width: auto;
}