.app {
  padding: 0px 12px 48px 12px;
  background-color: darkslateblue;
}

.app h1 {
  color: white;
  margin-block-end: 0em;
}

.list {
  display: grid;
  row-gap: 12px;
}

.game {
  display: grid;
  grid-template-columns: 12px [thumbnail] 150px 12px [content] 1fr 12px;
  grid-template-rows: 12px [top] 150px auto [bottom] 12px;

  background: lightgray;
  border-radius: 4px;
}

.game .content {
  grid-column: content;
  grid-row: top / bottom;

  align-items: start;
  display: flex;
  flex-direction: column;
}

.game .thumbnail {
  grid-column: thumbnail;
  grid-row: top;
  background-size: cover;
  background-position: center;
}

.game .title {
  font-size: 36px;
  font-weight: bold;
  line-height: 1em;
  margin-block-end: 0.2em;
  margin-block-start: -0.1em;
}

a {
  color: white;
  text-decoration: none;
}
a:hover {
  font-weight: bold;
}

.game a {
  font-size: 16px;
  color: black;
}